import cookie from 'vux/src/tools/cookie/index.js'

export const FREESTYLE = 0,
  STANDARD = 1,
  RENJU = 2

export const CONFIGS = [null, 'config210826.toml']

const state = {
  language: null,
  boardStyle: {
    boardColor: '#F4D03F',
    lineColor: '#000000',
    lineWidth: 0.03,
    coordColor: '#000000',
    coordFontStyle: '',
    coordFontFamily: 'sans-serif',
    starRadiusScale: 0.1,
    pieceBlack: '#000000',
    pieceWhite: '#FFFFFF',
    pieceStrokeWidth: 0.021,
    pieceStrokeBlack: '#000000',
    pieceStrokeWhite: '#000000',
    pieceScale: 0.95,
    indexColorBlack: '#FFFFFF',
    indexColorWhite: '#000000',
    indexFontStyle: 'bold',
    indexFontFamily: 'sans-serif',
    indexScale: 0.45,
    lastStepColor: '#E74C3C',
    lastStepScale: 0.15,
    winlineWidth: 0.12,
    winlineColor: '#2E86C1',
    bestMoveColor: '#E74C3C',
    thinkingMoveColor: '#3FF476',
    thoughtMoveColor: '#3C5EE7',
    lostMoveColor: '#FDFEFE',
    bestMoveScale: 0.12,
    realtimeMoveScale: 0.1,
    selectionStrokeWidth: 0.08,
    selectionStrokeColor: '#E74C3C',
    forbidStrokeWidth: 0.12,
    forbidStrokeColor: '#E74C3C',
    pvEvalFontStyle: 'bolder',
    pvEvalFontFamily: 'sans-serif',
    pvEvalScale: 0.45,
    pvEvalAlpha: 0.85
  },
  boardSize: 15,
  thinkTimeOption: 1,
  turnTime: 5000,
  matchTime: 9999000,
  maxDepth: 64,
  maxNodes: 0,
  threads: 1,
  nbest: 1,
  rule: 0, // 0- 1- 2,4- 5-
  configIndex: 1, // [0, CONFIGS.length)
  hashSize: 15, // TT(2**hashSize)K
  clickCheck: 0, // 0- 1- 2-
  indexOrigin: 0, // 
  showCoord: true,
  showDetail: true,
  showMultiPvEval: false,
  showIndex: true,
  showLastStep: true,
  showWinline: true,
  showForbid: true,
  aiThinkBlack: false,
  aiThinkWhite: false
}

const propertiesToSave = [
  'language',
  'boardSize',
  'thinkTimeOption',
  'turnTime',
  'matchTime',
  'maxDepth',
  'maxNodes',
  'threads',
  'nbest',
  'rule',
  'configIndex',
  'clickCheck',
  'showCoord',
  'showDetail',
  'showMultiPvEval',
  'showIndex',
  'showLastStep',
  'showWinline',
  'showForbid',
  'aiThinkBlack',
  'aiThinkWhite',
  'hashSize'
]

const boardPropertiesToSave = [
  'boardColor',
  'lastStepColor',
  'winlineColor',
  'bestMoveColor',
  'thinkingMoveColor',
  'thoughtMoveColor',
  'lostMoveColor'
]

import { version } from '@/../package.json'

const getters = {
  turnTime: state => {
    let turn = [state.turnTime, 7000, 45000, 0]
    return turn[state.thinkTimeOption]
  },
  matchTime: state => {
    let match = [state.matchTime, 180000, 900000, 0]
    return match[state.thinkTimeOption]
  },
  depth: state => {
    return state.thinkTimeOption == 0 ? state.maxDepth : 100
  },
  nodes: state => {
    return state.thinkTimeOption == 0 ? state.maxNodes : 0
  },
  gameRule: state => {
    switch (state.rule) {
      case 0:
      case 5:
        return FREESTYLE
      case 1:
        return STANDARD
      case 2:
      case 4:
        return RENJU
      default:
        throw Error('unknown rule')
    }
  }
}

function saveCookies() {
  let stateToSave = {}
  for (let p of propertiesToSave) stateToSave[p] = state[p]
  for (let p of boardPropertiesToSave) stateToSave[p] = state.boardStyle[p]
  cookie.set('GMKC_CFG_' + version, JSON.stringify(stateToSave), {
    raw: true,
    expires: 30
  })
}

const mutations = {
  setValue(state, payload) {
    state[payload.key] = payload.value
    if (propertiesToSave.includes(payload.key)) saveCookies()
  },
  setValueNoSave(state, payload) {
    state[payload.key] = payload.value
  },
  setBoardStyle(state, payload) {
    state.boardStyle[payload.key] = payload.value
    if (boardPropertiesToSave.includes(payload.key)) saveCookies()
  },
  setBoardStyleNoSave(state, payload) {
    state.boardStyle[payload.key] = payload.value
  }
}

const actions = {
  readCookies({ commit }) {
    let json = cookie.get('GMKC_CFG_' + version)
    if (!json) return

    let stateToRead = JSON.parse(json)
    for (let p of propertiesToSave) commit('setValueNoSave', { key: p, value: stateToRead[p] })
    for (let p of boardPropertiesToSave)
      commit('setBoardStyleNoSave', { key: p, value: stateToRead[p] })
  },
  clearCookies() {
    cookie.remove('GMKC_CFG_' + version)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
